import React from 'react'
import {Link} from 'react-router-dom';
import '../style/Nav.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faQuestionCircle,faHeart } from '@fortawesome/free-solid-svg-icons'
import {faDiscord} from "@fortawesome/free-brands-svg-icons"


const Nav = () => {
    return (
        <nav >
            <ul className="grid">
                <Link to='/' id="icon">
                    <li>GG </li>
                </Link>
                <Link to="Info" id="link1" >
                    <li>Info <FontAwesomeIcon icon={faQuestionCircle} className="icon"/></li>
                </Link>
                <Link to="Donate" id="link2" >
                    <li>Donar <FontAwesomeIcon icon={faHeart} className="icon"/></li>
                </Link>
                <a href="https://discord.gg/DDhNxt9vRB" target="blank" id="link3">Discord <FontAwesomeIcon icon={faDiscord} className="icon"/> </a>
            </ul>
        </nav>
    )
}

export default Nav
