import './App.css';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';
import Start from './components/Start'
import Info from './components/Info'
import Donar from './components/Donar'
import Dodge from './components/Dodge'
import Analysis from './components/Analysis'
import Play from './components/Play'
import Aproved from './components/Aproved'
import Intermediate from './components/Intermediate'
import Reproved from './components/Reproved'
import Rest from './components/Rest'


function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path='/' exact component={Start}/>
          <Route path='/Info' component={Info}/>
          <Route path='/Donate'  component={Donar}/>
          <Route path='/Dodge' component={Dodge}/>
          <Route path='/Analysis' component={Analysis}/>
          <Route path='/Play'component={Play}/>
          <Route path='/Aproved' component={Aproved}/>
          <Route path='/Intermediate'  component={Intermediate}/>
          <Route path='/Reproved' component={Reproved}/>
          <Route path='/Rest' component={Rest}/>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
