import React from 'react'
import {Link} from 'react-router-dom';
import Footer from './Footer';
import '../style/Start.css'
import Nav from './Nav';

const Start = () => {
    return (
        <div id='Start' >
            <Nav/>
            <div className="title grid">
                <h1>GitGud</h1>
                <h2>Toma el control de tus partidas</h2>
            </div>
            <main className="grid">
                <ul id='start-ul' className="flexUl">
                    <Link to='Dodge'>
                        <li className="btn" >
                            Dodge?
                        </li>
                    </Link>
                    <Link to="Play">
                        <li className="btn">
                            Jugar
                        </li>
                    </Link>
                    <Link to="Analysis">
                        <li className="btn">
                            An치lisis
                        </li>
                    </Link>
                </ul>
            </main>
            <Footer/>
        </div>
    )
}

export default Start
