import React from 'react'
import Footer from './Footer';
import Nav from './Nav';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHandHoldingUsd,faDollarSign } from '@fortawesome/free-solid-svg-icons'
import {faPaypal} from "@fortawesome/free-brands-svg-icons"


const Donar = () => {


    const Paypal = () => {
        window.open('https://www.paypal.com/donate?hosted_button_id=PYQPXW7A7QQG4&source=url')
    }
    const MercadoPago = () => {
        window.open('https://ceneka.net/mp/d/Marccos')
    }

    return (
        <div>
            <Nav/>
            <div className="title">
                <div className="donation">
                    <h4>Donar</h4>  <FontAwesomeIcon icon={faHandHoldingUsd} className="icon2"/>
                </div>
                <h5>Apoya el proyecto</h5>

            </div>
            <ul className="donate-ul">
                <li onClick={MercadoPago}> MercadoPago <FontAwesomeIcon icon={faDollarSign} className="icon3"/></li>
                <li onClick={Paypal}>Paypal <FontAwesomeIcon icon={faPaypal} className="icon3"/></li>
            </ul>
            <Footer/>
        </div>
    )
}

export default Donar
